      üéâ ÂÖ®Êñ∞ <strong>Êõ¥Âø´ÈÄü„ÄÅÊ∫ñÁ¢∫„ÄÅËá™ÂãïÂåñ</strong> ÁöÑÊäΩÁ±§Â∑•ÂÖ∑ Chrome Êì¥ÂÖÖÂ•ó‰ª∂‰∏äÁ∑ö‰∫ÜÔºÅ<br>
      üëâ <a href="https://github.com/artale-tool/discord-lottery-extension" target="_blank">ÈªûÊàëÂâçÂæÄ GitHub ‰∏ãËºâ</a>
    </p>
    <p class="desc">
      Ë´ãÂ∞á DC ÁïôË®ÄË§áË£Ω‰∏¶Ë≤ºÂÖ•‰ª•‰∏ãËº∏ÂÖ•Ê°Ü„ÄÇÁî±Êñº DC ÈôêÂà∂‰∏ÄÊ¨°ÂèØË§áË£ΩÁöÑÁïôË®ÄÊï∏ÈáèÔºåÂª∫Ë≠∞ÊØèÊ¨°Ë§áË£ΩÁ¥Ñ 80 ÂâáÁïôË®Ä„ÄÇÊØèÊ¨°Ë§áË£ΩÊôÇÔºåË´ãÂãôÂøÖÁ¢∫‰øùËàá‰∏ä‰∏ÄÊ¨°Ë§áË£ΩÁöÑÂÖßÂÆπ‰πãÈñìÊúâÊèõË°åÂàÜÈöîÔºå‰ª•ÈÅøÂÖçË≥áÊñôÊ∑∑Ê∑ÜÊàñÈÅ∫Êºè„ÄÇ
    </p>

    <div class="main">
      <!-- Â∑¶Ê¨Ñ -->
      <div class="left-panel">
        <textarea v-model="rawText" rows="8" placeholder="Ë´ãË≤º‰∏äÁïôË®Ä"></textarea>

        <div class="options">
          <div class="option-group">
            <label>Êé®ÊñáÈóúÈçµÂ≠óÔºö
              <input v-model="keyword" />
            </label>
          </div>

          <div class="option-group">
            <label>Êé®ÊñáÊï∏Â≠ó‰ΩçÁΩÆÔºö
              <select v-model="numberPosition">
                <option value="after">Êï∏Â≠óÂú®ÈóúÈçµÂ≠óÂæåÔºàÂ¶Ç Êé®1Ôºâ</option>
                <option value="before">Êï∏Â≠óÂú®ÈóúÈçµÂ≠óÂâçÔºàÂ¶Ç 1Êé®Ôºâ</option>
                <option value="any">‰∏çÈôê‰ΩçÁΩÆÔºàÂêåÊôÇÊîØÊè¥ Êé®1 Âíå 1Êé®Ôºâ</option>
              </select>
            </label>
          </div>

          <div class="option-group">
            <label>ÈáçË§áÂ∏≥ËôüËôïÁêÜÔºö
              <select v-model="duplicateMode">
                <option value="exclude">ÊéíÈô§ÈáçË§áÂ∏≥Ëôü</option>
                <option value="first">ÂÉÖ‰øùÁïôÁ¨¨‰∏ÄÁ≠Ü</option>
                <option value="allow">ÂÖÅË®±ÈáçË§áÂèÉÂä†</option>
              </select>
            </label>
          </div>

          <button @click="parseText">Êï¥ÁêÜÁïôË®Ä</button>
        </div>

        <!-- ÈáçË§áÂ∏≥ËôüÂêçÂñÆ -->
        <div v-if="filteredDuplicates.length" class="duplicates-list">
          <h2 class="duplicates-title">ÈáçË§áÁïôË®ÄÂ∏≥ËôüÔºà{{ filteredDuplicates.length }} ‰ΩçÔºâ</h2>
          <p class="note-text">‚Äª ÈÉ®ÂàÜ‰ΩøÁî®ËÄÖ ID ÂèØËÉΩÁõ∏Âêå‰ΩÜÂØ¶ÈöõÁÇ∫‰∏çÂêåÂ∏≥ËôüÔºåÂèØËÉΩÊúÉË™§Âà§ÁÇ∫ÈáçË§áÂ∏≥ËôüÔºåË´ãÁâπÂà•ÁïôÊÑèÊòØÂê¶Ë™§ÊéíÈô§ÊúâÊïàÁïôË®Ä„ÄÇ</p>
          <ul class="duplicates-ul">
            <li v-for="(item, idx) in filteredDuplicates" :key="idx" class="duplicate-item">
              <span>
                {{ item.account }} - ÁïôË®Ä {{ item.count }} Ê¨°Ôºà{{ item.numbers.map(n => 'Êé®' + n).join(', ') }}Ôºâ
              </span>
              <button @click="includeAccount(item.account)" class="btn-restore">ÈÇÑÂéüÂèÉÂä†</button>
            </li>
          </ul>
        </div>

        <!-- Êé®ËôüÈáçË§áÂêçÂñÆ -->
        <div v-if="duplicateNumbers.length" class="duplicates-list">
          <h2 class="duplicates-title">Êé®ÊñáËôüÁ¢ºÈáçË§áÔºà{{ duplicateNumbers.length }} Á≠ÜÂ∑≤ÁßªÈô§Ôºâ</h2>
          <p class="note-text">‚Äª Âêå‰∏ÄÂÄãÊé®ËôüÂè™ËÉΩ‰øùÁïôÁ¨¨‰∏ÄÂÄãÁïôË®ÄÔºåÂÖ∂È§òÂ∞áËá™ÂãïÁï•ÈÅé„ÄÇ</p>
          <ul class="duplicates-ul">
            <li v-for="(item, idx) in duplicateNumbers" :key="idx">
              {{ item.account }} - Êé®{{ item.number }}
            </li>
          </ul>
        </div>

        <!-- ÂèÉÂä†ÂêçÂñÆ -->
        <div v-if="parsed.length" class="participants-list">
          <h2 class="participants-title">ÂèÉÂä†ÂêçÂñÆÔºà{{ parsed.length }}‰∫∫ÔºâÔºö</h2>
          <ul class="participants-ul">
            <li v-for="(entry, i) in parsed" :key="i">
              {{ entry.account }} - Êé®{{ entry.number }}
            </li>
          </ul>
        </div>
      </div>

      <!-- Âè≥Ê¨Ñ -->
      <div class="right-panel">
        <label>ÊäΩÂπæ‰∫∫Ôºö
          <input type="number" v-model.number="drawCount" min="1" :max="parsed.length || 1" />
        </label>

        <button @click="drawWinners" :disabled="parsed.length === 0">ÊäΩÁ±§</button>

        <div v-if="winners.length" class="winners-list">
          <h2>üéâ ‰∏≠ÁçéÂêçÂñÆÔºà{{ winners.length }}‰∫∫ÔºâÔºö</h2>
          <ul class="winners-ul">
            <li v-for="(winner, i) in winners" :key="i">
              {{ winner.account }} - Êé®{{ winner.number }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rawText: '',
      keyword: 'Êé®',
      numberPosition: 'after',
      duplicateMode: 'exclude',
      parsed: [],
      drawCount: 1,
      winners: [],
      duplicates: [],
      duplicateNumbers: [],
      entries: [],
      manualIncluded: [],
    }
  },
  computed: {
    filteredDuplicates() {
      return this.duplicates.filter(d => !this.manualIncluded.includes(d.account))
    }
  },
  methods: {
    parseText() {
      const lines = this.rawText.split('\n').map(l => l.trim())
      const entries = []

      const isPureMetaLine = (line) => !line || /^\[.*\]$/.test(line) || /^‚Äî/.test(line) || /^(Êò®Â§©|‰∏äÂçà|‰∏ãÂçà|Êôö‰∏ä|ÂáåÊô®).*/.test(line)
      const escapeRegExp = (str) => str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')
      const escapedKeyword = escapeRegExp(this.keyword)
      const isPushLine = (line) => new RegExp(`^${escapedKeyword}\\d+$`).test(line)

      for (let i = 0; i < lines.length; i++) {
        const line = lines[i]
        
        let match = null;
        if (this.numberPosition === 'after') {
          match = line.match(new RegExp(`^${escapedKeyword}(\\d+)$`));
        } else if (this.numberPosition === 'before') {
          match = line.match(new RegExp(`^(\\d+)${escapedKeyword}$`));
        } else if (this.numberPosition === 'any') {
          match = line.match(new RegExp(`(?:^${escapedKeyword}(\\d+)$)|(?:^(\\d+)${escapedKeyword}$)`));
          if (match) {
            match[1] = match[1] || match[2]; // Áµ±‰∏ÄÂèñÂá∫Êï∏Â≠ó
          }
        }

        if (match) {
          const number = parseInt(match[1])
          let account = null
          for (let j = i - 1; j >= 0 && i - j <= 5; j--) {
            const candidate = lines[j]
            if (!candidate || isPureMetaLine(candidate) || isPushLine(candidate)) continue
            account = candidate.includes('‚Äî') ? candidate.split('‚Äî')[0].trim() : candidate.trim()
            break
          }
          if (account) entries.push({ account, number })
        }
      }

      // ÁßªÈô§Êé®ËôüÈáçË§áÔºåÂè™‰øùÁïôÁ¨¨‰∏ÄÁ≠Ü
      const seenNumbers = new Set()
      const uniqueEntries = []
      const duplicateNumbers = []
      for (const entry of entries) {
        if (!seenNumbers.has(entry.number)) {
          seenNumbers.add(entry.number)
          uniqueEntries.push(entry)
        } else {
          duplicateNumbers.push(entry)
        }
      }

      this.entries = uniqueEntries
      this.duplicateNumbers = duplicateNumbers

      // ÈáçË§áÂ∏≥ËôüÁµ±Ë®à
      const counts = {}
      this.entries.forEach(e => {
        counts[e.account] = counts[e.account] ? [...counts[e.account], e.number] : [e.number]
      })

      this.duplicates = Object.entries(counts)
        .filter(([, nums]) => nums.length > 1)
        .map(([acc, nums]) => ({ account: acc, count: nums.length, numbers: nums }))

      this.manualIncluded = []
      this.updateParsedList()
      this.winners = []
    },

    updateParsedList() {
      const counts = {}
      this.entries.forEach(e => {
        counts[e.account] = counts[e.account] ? [...counts[e.account], e.number] : [e.number]
      })

      if (this.duplicateMode === 'exclude') {
        this.parsed = this.entries.filter(
          (e, i, self) =>
            (self.findIndex(x => x.account === e.account) === i && counts[e.account].length === 1) ||
            this.manualIncluded.includes(e.account)
        )
      } else if (this.duplicateMode === 'first') {
        this.parsed = this.entries.filter(
          (e, i, self) =>
            self.findIndex(x => x.account === e.account) === i ||
            this.manualIncluded.includes(e.account)
        )
      } else {
        this.parsed = this.entries
      }
    },

    includeAccount(account) {
      if (!this.manualIncluded.includes(account)) {
        this.manualIncluded.push(account)
        this.updateParsedList()
      }
    },

    drawWinners() {
      const maxDraw = this.parsed.length
      if (this.drawCount < 1) this.drawCount = 1
      if (this.drawCount > maxDraw) this.drawCount = maxDraw

      // Fisher-Yates Ê¥óÁâåÊºîÁÆóÊ≥ï
      const shuffled = [...this.parsed]; // Ë§áË£Ω parsed ÈÅøÂÖç‰øÆÊîπÂéüÂßãË≥áÊñô
      for (let i = shuffled.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
      }
      this.winners = shuffled.slice(0, this.drawCount);
    },
  },
}
</script>

